    submitForm(val){
      this.$refs['form'].validate((valid,obj) => {
        if (valid) {
          if (this.form.rentNum=="") {
            this.alertMsg("");
            return;
          } else if (!/^\d+(?=\.{0,1}\d+$|$)/.test(this.form.rentNum)) {
            this.alertMsg("");
            return;
          } else if(!this.checkPhotos()) {
            this.alertMsg("");
            return;
          } else {
            this.submitAction(val);
          }
        } else {
          for(let key in obj){
            if(this.rules[key][0].message){
              document.getElementById(key).scrollIntoView();
              this.alertMsg(this.rules[key][0].message);
            }
            break;
          }
          return false;
        }
      });
    },
    saveForm(val){//
      if(this.form.buildTypeID==""||this.form.buildRecordId==""||this.form.position==""){
        this.alertMsg("");
        return;
      } else {
        this.submitAction(val);
      }
    },
    submitAction(val){
      this.getBuildNameById();
      console.log("this.form.pointName...",this.form.pointName);
      // return false;
      // 
      const conditionInfoBuild={//
        floorArea:this.form.floorArea,//
        buildFloor:this.form.buildFloor,//
        lodgingRatio:this.form.lodgingRatio,//
        useRatio:this.form.useRatio,//
        buildNum:this.form.buildNum//
      }
      const conditionInfoPark={//
        floorParkArea:this.form.floorParkArea,//
        buildParkFloor:this.form.buildParkFloor,//
        lodgingParkRatio:this.form.lodgingParkRatio,//
        useParkRatio:this.form.useParkRatio,//
        coverParkNum:this.form.coverParkNum//
      }
      const otherInfo={//
        unicorn:this.form.unicorn,//
        mobile:this.form.mobile,//
        telecom:this.form.telecom,//
        power:this.form.power,//
        putEnvironment:this.form.putEnvironment,//
        competingGoods:this.form.competingGoods//
      }
      const baseInfo={//
        rentWay:this.form.rentWay,//
        rentNum:this.form.rentNum,//
        electric:this.form.electric,//
        service:this.form.service,//
        total:this.form.total,//
      }
      let params={
        point_id:this.id,//Id
        point_name:this.form.pointName+this.form.position,
        point_position:this.form.position,
        point_level: this.pointLevel,//
        point_score: this.pointScore,//
        cooperate:this.form.cooperation,
        point_status:val,//0 1
        build_type_id: this.form.buildType,//,
        build_record_id:this.form.buildRecordId,
        point_basic_info: baseInfo,
        point_score_info:"",//
        point_other_info: otherInfo,
        point_licence_pic:[""],//
        point_position_pic:["",""],//
        point_company_pic:["","","","",""],//
        point_plan:[""],//
        contact_tel:this.form.contactTel,
        contact_name:this.form.contactName
      }
      if(this.form.buildTypeValue==""){
        params.point_score_info=conditionInfoBuild;
      }else if(this.form.buildTypeValue==""){
        params.point_score_info=conditionInfoPark;
      }else if(this.form.buildTypeValue==""){
        baseInfo.area=this.form.area;//
        baseInfo.floor=this.form.floor;//
        baseInfo.humanTraffic=this.form.humanTraffic;//
      }else if(this.form.buildTypeValue==""){
        baseInfo.companyType=this.form.companyType;//
        // baseInfo.companyNum=this.form.companyNum;//
        // baseInfo.scale=this.form.scale;//
      }else if(this.form.buildTypeValue==""){
        baseInfo.hospitalLevel=this.form.hospitalLevel;//
        baseInfo.receptionNum=this.form.receptionNum;//
        baseInfo.medicalNum=this.form.medicalNum;//
        baseInfo.hospitalHuman=this.form.hospitalHuman;//
      }
      console.log("params",params);
      axios.post(this.submitFormUrl,params)
      .then((response)=> {
        let data = response.data;
        console.log("data",data);
        if(data.error_code==0){
          let msg = (val==0)?'':'';
          this.alertMsg(msg,'success');
          this.$router.push({name:"pointList"});
        }else{
           this.alertMsg('',data.msg);
        }
      })
      .catch((error)=> {
        this.alertMsg(error);
      });
    }
  },
  components: {}
}
</script>
<style>
li {list-style-type:none;}
.table-line{
  margin-bottom:20px;
  margin-left:30px;
}
.sub-title{
  font-size:14px;
}
.pic-line{
  margin-left:100px;
}
.pic{
  width:300px;
  height: auto;
  margin-bottom: 10px;
}
.pic+.pic{
  margin-left:10px !important;
}
.pic:nth-child(3n+1){
  margin-left:50px !important;
}
.sub-title{
  width:85px;
  font-size:14px;
  color:#333;
}
.el-form-item {
  margin-bottom: 20px;
}
.el-checkbox{
  margin-right:30px;
}
.el-checkbox+.el-checkbox{
  margin-left:0;
}
.build-name{
  position: relative;
}
.charter-money{
  width:60px;
  height: 20px;
  line-height: 20px;
  text-align:center;
  margin:0 10px;
}
.charter-box{
  float:left;
  margin-right:10px;
}
.charter-money .el-input__inner{
  height: 20px;
  line-height: 20px;
}
.table-line{
  margin-bottom:20px;
  margin-left:30px;
}
.mobile:before{
  position: absolute;
  left:27px;
  top:11px;
  display:inline-block;
  content: "*";
  color:red;
}

</style>



